import React from 'react';
import styled from 'styled-components';
import Throws from './throws';

const Score = (props) => {
	const { scoreData, selected } = props;
	let scoreSum = '';

	if (!scoreData.every(isNull)) {
		scoreSum = arrSum(scoreData);
	}

	if (selected) {
		return <SelectedScoreBox>
			{scoreSum}
			<Throws scoreData={scoreData} />
		</SelectedScoreBox>;
	}

	return <ScoreBox>
		{scoreSum}
		<Throws scoreData={scoreData} />
	</ScoreBox>;
}

const arrSum = arr => arr.reduce((a,b) => a + b, 0)

const isNull = value => value == null

const ScoreBox = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	position: relative;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}

	:first-of-type {
		margin-top: 58px;
	}
`

const SelectedScoreBox = styled(ScoreBox)`
	outline: solid 2px #317ae2;
`

export default Score;