import React from 'react';
import { LeagueConsumer } from "./index.js";
import Frame from './frame';

const Scoreboard = (props) => {
	return <LeagueConsumer>
		{(context) => (
			createFrames(context.state)
		)}
	</LeagueConsumer>
}

const createFrames = (state) => {
	const { target } = state;
	const start = target.frame < 3 ? 0 : target.frame - 2;
	const limit = target.frame + 1;

	let frames = [];

	for (let i = start; i < limit; i++) {
		frames.push(<Frame key={`${target.throw}-${i}`} frameNo={i} />)
	}

	return frames;
}

export default Scoreboard;
