import React, { useContext } from 'react';
import { LeagueContext } from './index.js';
import SummaryRow from './summary-row.js';
import { countBy, filter, concat } from 'lodash';

const Summary = (props) => {
	const context = useContext(LeagueContext);
	const { scoreMatrix } = context.state;

	return renderRows(scoreMatrix);
}

const renderRows = (scoreMatrix) => {
	let destinationData = {5: [], 6: [], 7: [], 8: [], 9: [], 10: []};
	let tries = [];

	scoreMatrix.forEach(function(distances, index) {
		for (const [distance, array] of Object.entries(distances)) {
			destinationData[distance] = concat(destinationData[distance], array);
		}
	});

	for (const [distance, array] of Object.entries(destinationData)) {
		const filteredData = filter(array, function(x) { return x === 1 || x === 0; });
		const count = countBy(filteredData, Math.floor);

		destinationData[distance] = count;
		tries.push(<SummaryRow count={count} key={distance} />)
	}

	return tries;
}

export default Summary;
