import React, { useContext } from 'react';
import { LeagueContext } from './index.js';
import Score from './score';

import styled from 'styled-components';

const Frame = (props) => {
	const { frameNo } = props;
	const context = useContext(LeagueContext);
	const { scoreMatrix, target } = context.state;
	
	return <FrameWrapper>
		{renderScores(scoreMatrix, target, frameNo)}
	</FrameWrapper>
}

const renderScores = (scoreMatrix, target, frameNo) => {
	const scores = scoreMatrix[frameNo];

	let scoreArray = [];

	scoreArray.push(<FrameNumber key={frameNo}>{frameNo + 1}</FrameNumber>)

	if (scores) {
		Object.keys(scores).map(key => {
			const keyString = `${target.frame}${target.distance}${target.throw}${key}`;

			if (target.frame === frameNo && target.distance === Number(key)) {
				return scoreArray.push(<Score selected scoreData={scores[key]} key={keyString} />);
			}

			return scoreArray.push(<Score scoreData={scores[key]} key={keyString} />);
		});
	}

	return scoreArray
}

const FrameWrapper = styled.div`
	width: 52px;
	padding: 4px 2px;
`

const FrameNumber = styled.div`
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	position: relative;
`

export default Frame;