import React from 'react';
import styled from 'styled-components';

const Throws = (props) => {
	const { scoreData } = props;
	
	return <ThrowsHolder>
		{renderThrows(scoreData)}
	</ThrowsHolder>;
}

const renderThrows = (scoreData) => {
	let tries = [];

	scoreData.forEach(function(item, index) {
		if (item === 0) {
			return tries.push(<MissMark key={index} />)
		}

		if (item === 1) {
			return tries.push(<HitMark key={index} />)
		}

		return tries.push(<NoMark key={index} />)
	});

	return tries;
}

const ThrowsHolder = styled.div`
	position: absolute;
	right: 0px;
	top: 3px;
	height: 50px;
	width: 10px;
`

const HitMark = styled.div`
	height: 3px;
	width: 3px;
	border-radius: 50%;
	border: solid 2px #317ae2;
	background: #317ae2;
	margin-bottom: 2px;
	box-shadow: 0 0 1px 1px rgba(0,0,0,0.04), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
`

const MissMark = styled(HitMark)`
	background: transparent;
	border: solid 2px #317ae2;
`

const NoMark = styled(HitMark)`
	background: white;
	border: solid 2px white;
`

export default Throws;