import React from 'react';
import { LeagueConsumer } from './index.js';
import Icons from '../../assets/icons.svg';
import styled from 'styled-components';

const Key = (props) => {
	const { keyName, keyValue, fontColor } = props;

	return <LeagueConsumer>
		{(context) => (
			<KeyButton fontColor={fontColor} onClick={() => context.state.keyPress(keyValue, context)}>
				<Icon className={keyName} color={fontColor}>
					<use xlinkHref={`${Icons}#${keyName}`}></use>
				</Icon>
			</KeyButton>
		)}
	</LeagueConsumer>
};

const KeyButton = styled.button`
	border-radius: 2px;
	height: 70px;
	width: 90px;
	display: flex;
	flex-direction: column;
	margin: 4px 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	text-align: -webkit-center;
	padding-top: 20px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	cursor: pointer;
	color: ${props => props.fontColor || '#317ae2'};
	font-size: 24px;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const Icon = styled.svg`
	display: inline-flex;
	flex: 0 0 auto;
	box-sizing: border-box;
	width: 24px;
	height: 24px;
	fill: ${props => props.color};
	user-select: none;
`

export default Key;
