import React, { createContext, useEffect } from 'react';
import styled from 'styled-components';
import Scoreboard from './scoreboard';
import Keyboard from './keyboard';
import Distances from './distances';
import Totals from './totals';
import Summary from './summary';
import useUndo from './use-history';
import { distances } from '../../utils/distances';
import { cloneDeep } from 'lodash';
import ez3 from '../../assets/easy3-80.png';

const LeagueContext = createContext({});
const LeagueProvider = LeagueContext.Provider;
const LeagueConsumer = LeagueContext.Consumer;

const initialState = {
	noOfFrames: 20,
	totalThrows: 0,
	currentTotal: 0,
	scoreMatrix: [],
	showTotal: true,
	target: {
		frame: 0,
		distance: distances[0],
		throw: 0
	},
	keyPress: (keyValue, stateEngine) => handleKeyPress(keyValue, stateEngine),
};

const LeagueNight = () => {
	const stateEngine = useUndo(initialState);

	useEffect(() => {
		createScoreMatrix(stateEngine);
	}, []);

	if ((stateEngine.state.target.frame + 1) > stateEngine.state.noOfFrames) {
		return <LeagueContext.Provider value={stateEngine}>
			<HeaderData>{renderHeader(stateEngine)}</HeaderData>
			<LeagueMain>
				<Distances />
				<SummaryMain>
					<Summary />
				</SummaryMain>
				<Totals />
			</LeagueMain>
			<KeyboardContainer />
			<Icon src={ez3} />
		</LeagueContext.Provider>;
	}

	return <LeagueContext.Provider value={stateEngine}>
			<HeaderData>{renderHeader(stateEngine)}</HeaderData>
			<LeagueMain>
				<Distances />
				<Scoreboard />
				<Totals />
			</LeagueMain>
			<KeyboardContainer>
				<Keyboard />
			</KeyboardContainer>
			<Icon src={ez3} />
		</LeagueContext.Provider>
}

const handleKeyPress = (keyValue, stateEngine) => {
	const { state, set, undo, canUndo } = stateEngine;

	if (keyValue === 'UNDO' && canUndo) {
		return undo();
	}

	if (state.totalThrows === (state.noOfFrames * 5)) {
		return;
	}

	set({
		...state,
		scoreMatrix: setScoreMatrix(stateEngine, keyValue),
		totalThrows: state.totalThrows + 1,
		target: setTarget(stateEngine, keyValue),
		currentTotal: setCurrentTotal(state, keyValue)
	});
}

const createScoreMatrix = (stateEngine) => {
	const { state, set } = stateEngine;
	let matrix = [];

	for (let i = 0; i < state.noOfFrames; i++) {
		let tempObject = {};

		for (const distance of distances) {
			tempObject[distance] = [null, null, null, null, null];
		}
		matrix.push(tempObject);
	}

	set({ ...state, scoreMatrix: matrix });
}

const setScoreMatrix = (stateEngine, keyValue) => {
	const { target, scoreMatrix } = cloneDeep(stateEngine.state);
	const distance = scoreMatrix[target.frame][target.distance];

	distance[target.throw] = keyValue;

	return scoreMatrix;
}

const setTarget = (stateEngine, keyValue) => {
	const { target } = cloneDeep(stateEngine.state);

	target.throw = target.throw < 4 ? target.throw + 1 : 0;

	if (target.throw === 0) {
		target.frame = target.frame + 1;
	}

	if (keyValue === 1 && target.distance === 10) {
		return target;
	} else if (keyValue === 1 && target.distance < 10) {
		target.distance = target.distance + 1;
	} else if (keyValue === 0 && target.distance > 5) {
		target.distance = target.distance - 1;
	} else if (keyValue === 0 && target.distance === 5) {
		return target;
	}

	return target;
}

const setCurrentTotal = (state, keyValue) => {
	const { scoreMatrix, target } = cloneDeep(state);
	const throwScore = target.distance * keyValue;

	let totalScore = 0;

	scoreMatrix.forEach((scores) => {
		for (let [key, values] of Object.entries(scores)) {
			for (const value of values) {
				totalScore += key * value;
			}
		}
	});

	return totalScore + throwScore;
}

const renderHeader = (stateEngine) => {
	if ((stateEngine.state.target.frame + 1) > stateEngine.state.noOfFrames) {
		return `Game Over`;
	}

	return `Frame ${stateEngine.state.target.frame + 1}`
}

const Icon = styled.img`
	width: 100px;
	height: 100px;
	display: block;
	margin: 0 auto;
`;

const LeagueMain = styled.div`
	width: 100%;
	height: 100%;
	padding: 10px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background: #f7f7f7;
`;

const SummaryMain = styled.div`
	width: 350px;
	height: 100%;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: #f7f7f7;
`;

const KeyboardContainer = styled.div`
	padding: 0 0 50px;
	width: 100%;
	background: #f7f7f7;
	display: flex;
	flex-direction: row;
	justify-content: center;
	box-sizing: border-box;
`;

const HeaderData = styled.span`
	background: #f7f7f7;
	width: 100%;
	text-align: center;
	display: flex;
	flex-direction: column;
	padding-top: 20px;
	font-size: 1.3em;
`

export {
	LeagueContext,
	LeagueProvider,
	LeagueConsumer
};

export default LeagueNight;