import React from 'react';
import styled from 'styled-components';

const HitBar = (props) => {
	const { width, count } = props;

	return (
		<>
			<HitBarX width={width} />
			<HitCount>{count || 0}</HitCount>
		</>
	)
}

const HitBarX = styled.div`
	height: 10px;
	width: ${props => props.width ? props.width : '0'};
	margin: 2px 0;
	background-color: #61c786;
`;

const HitCount = styled.span`
	position: absolute;
	right: 7px;
	bottom: 7px;
	color: #61c786;
	font-size: 14px;
	background-color: white;
	padding: 1px;
`;

export default HitBar;
