import React from 'react';
import HitBar from './hit-bar';
import MissBar from './miss-bar';
import styled from 'styled-components';

const SummaryRow = (props) => {
	return <SummaryRowHolder>
		{renderSummary(props)}
	</SummaryRowHolder>;
}

const renderSummary = (props) => {
	const hitWidth = calculatWidth(props.count[0]);
	const missWidth = calculatWidth(props.count[1]);

	return (
		<>
			<MissBar width={hitWidth} count={props.count[0]} />
			<HitBar width={missWidth} count={props.count[1]} />
		</>
	)
}

const calculatWidth = (count) => {
	if (!count) {
		return '0px';
	}

	return `${count * 7}px`;
}

const SummaryRowHolder = styled.div`
	min-height: 50px;
	width: 100%;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	padding: 12px 0 0 8px;
	flex-basis: 100%;
	opacity: initial;
	background: rgb(255, 255, 255);
	text-align: center;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	position: relative;

	:first-of-type {
		margin-top: 58px;
	}
`

export default SummaryRow;
