import React from 'react';
import { LeagueConsumer } from './index.js';
import Key from './key.js'

const Keyboard = (props) => {
	return <LeagueConsumer>
		{(context) => (
			<>
			<Key key={1} keyName={'Hit'} keyValue={1} fontColor={'#08a742'} />
			<Key key={2} keyName={'Miss'} keyValue={0} fontColor={'#f94839'} />
			<Key key={3} keyName={'Undo'} keyValue={'UNDO'} fontColor={'#317ae2'} />
			</>
		)}
	</LeagueConsumer>
};

export default Keyboard;


