import React from 'react';
import styled from 'styled-components';

const MissBar = (props) => {
	const { width, count } = props;

	return (
		<>
			<MissBarX width={width} />
			<MissCount>{count || 0}</MissCount>
		</>
	)
}

const MissBarX = styled.div`
	height: 10px;
	width: ${props => props.width ? props.width : '0'};
	margin: 2px 0;
	background-color: #fb8a80;
`;

const MissCount = styled.span`
	position: absolute;
	right: 7px;
	top: 7px;
	color: #fb8a80;
	font-size: 14px;
	background-color: white;
	padding: 1px;
`;

export default MissBar;
