import React, { useContext } from 'react';
import { LeagueConsumer, LeagueContext } from './index.js';
import styled from 'styled-components';
import Icons from '../../assets/icons.svg';

const Totals = (props) => {
	const context = useContext(LeagueContext);
	const { state, set } = context;
	const { showTotal, currentTotal } = state;
	const icon = showTotal ? 'Hide' : 'Show';

	const toggleTotalVisibility = () => {
		set({
			...state,
			showTotal: !showTotal
		});
	}

	return <LeagueConsumer>
		{(context) => (
			<TotalsWrapper>
				<Header>Total:</Header>
				<ScoreBox>{showTotal ? currentTotal : '-'}</ScoreBox>
				<ToggleButton onClick={() => toggleTotalVisibility()}>
					<Icon>
						<use xlinkHref={`${Icons}#${icon}`}></use>
					</Icon>
				</ToggleButton>
			</TotalsWrapper>
		)}
	</LeagueConsumer>
}

const TotalsWrapper = styled.div`
	width: 52px;
	padding: 4px 2px;
	margin-left: 16px;
`

const Header = styled.span`
	text-align: center;
	display: flex;
	flex-direction: column;
	padding: 18px 0;
`

const ScoreBox = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	position: relative;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const ToggleButton = styled.button`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	opacity: initial;
	background: transparent;
	box-shadow: 0 0 0 0 rgba(0,0,0,0), 0 0 0 0 rgba(0,0,0,0), 0 0 0 0 rgba(0,0,0,0);
	border: 0;
	text-align: center;
	text-align: -webkit-center;
	padding-top: 12px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	cursor: pointer;
	color: ${props => props.fontColor || '#317ae2'};
	font-size: 24px;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const Icon = styled.svg`
	display: inline-flex;
	flex: 0 0 auto;
	box-sizing: border-box;
	width: 24px;
	height: 24px;
	fill: #317ae2;
	user-select: none;
`

export default Totals;
